@import url(https://fonts.googleapis.com/css?family=Montserrat);

.validation input {
  width: 200px;
  height: 30px;
  margin-bottom: 30px;
  margin-top: 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.red input {
  border: 1px solid red;
}

input:focus {
  outline: none !important;
}

.profile {
  margin: auto;
  height: 400px;
  width: 230px;
}

h1 {
  width: 100px;
  height: 5px;
  color: red;
}

label {
  margin-right: 90px;
  font-family: Montserrat;
}

button {
  margin-top: 30px;
  width: 200px;
  height: 30px;
  background-color: #fff;
  border: 1px solid black;
  font-weight: bold;
  color: white;
  background-color: #2cdb26;
}

.bigDiv {
  margin: auto;
  width: 230px;
  height: 300px;
}